<template>
  <div class="home wh100">
    <header class="header-footer flexcenter" @click="test">
      利豪笔记
      <div class="switch" @click="handleNavSwitch">
        <i :class="['el-icon-arrow-right',navFlag ? 'deg180': '']"></i>
      </div>
    </header>
    <main class="main">
      <vue-scroll
        ref="vuescroll"
        :ops="vueScrollOption"
        @handle-resize="myScrollResize"
        @handle-scroll="myScrolling"
      >
        <article v-show="showContentFlag == 'list'" class="list wh100">
          <section class="section" v-for="(n,index) in showList" :key="index">
            <div class="wrap">
              <div
                :class="['index',list.all[byFileNameGetListAllIndex(n)].cacheFlag ? 'index-active': '',loadingFlag ? 'index-loading': '']"
                @click="handleTapIcon"
                v-longtap="handleLongtapIcon"
                :title="n"
              >{{index+1}}</div>
              <div
                :class="['title',list.all[byFileNameGetListAllIndex(n)].cacheFlag ? 'title-active': '']"
                @click="handleTapTitle"
                v-longtap="handleLongtapTitle"
                :title="n"
              >{{n}}</div>
            </div>
          </section>
        </article>
        <m-d v-show="showContentFlag == 'md'" class="md wh100" :all-index="mdIndex" :list="list"></m-d>
        <i-m-g
          v-show="showContentFlag == 'img'"
          class="img wh100"
          :all-index="imgIndex"
          :list="list"
        ></i-m-g>
        <collectList
          v-show="showContentFlag == 'collect'"
          :all-index="collectIndex"
          :list="list"
          @remove="handleRemoveCollect"
          @skip="handleCollectSkip"
        ></collectList>
      </vue-scroll>
      <node-nav
        class="node-nav"
        :list="list"
        :class="navFlag ? 'node-nav-active' : ''"
        @selectnav="handleChangeNav"
      ></node-nav>
    </main>
    <footer
      class="header-footer flexcenter"
      @click="handleTapFooter"
      v-longtap="handleLongtapFooter"
      :class="collectingFlag ? 'collecting' : ''"
    >
      <span>{{collectingFlag ? '收藏成功~':'仅供学习和交流'}}</span>
      <div class="subheading" v-show="showContentFlag != 'list' && (showContentFlag != 'collect')">
        点我返回
        {{showContentFlag != 'img' ?'，长按收藏' : ''}}
        </div>
    </footer>
    <div class="shade flexcenter" @click="closeShade" v-show="shadeFlag">{{shadeContent}}</div>
    <collect-tip v-show="showContentFlag == 'md'" :all-index="mdIndex" :list="list"></collect-tip>
  </div>
</template>

<script>
import IMG from "./IMG.vue";
import MD from "./MD.vue";
import NodeNav from "./NodeNav.vue";
import collectList from "./collectList.vue";
import collectTip from "./collectTip.vue";

export default {
  name: "Home",
  data() {
    return {
      loadingFlag: false,
      collectingFlag: false,
      showContentFlag: "",
      shadeFlag: false,
      shadeContent: "",
      navFlag: false,
      imgIndex: 0,
      mdIndex: 0,
      collectIndex: 0,
      oldShowContentFlag: "",
      list: {
        all: [],
        typeName: []
      },
      baseUrl: "",
      showList: [],
      myJSON: {},
      allowCacheType: ["md", "txt", "png", "webp", "jpg", "gif", "bmp", "jpeg"],
      vueScrollOption: {
        scrollPanel: {
          scrollingX: false,
          easing: "easeOutCubic"
        },
        bar: { background: "#aaa" }
      },
      scrollData: {
        // 本次运行记录
        list: "0", //在list的位置,px
        md: {
          allIndex: {
            top: "", // 百分数
            size: ""
          }
        }
      }
    };
  },
  methods: {
    test() {
      this.$refs.vuescroll.scrollTo({ y: "100" }, 0);
    },
    enterMd() {
      if (this.scrollData.md[this.mdIndex]) {
        setTimeout(() => {
          this.$refs.vuescroll.scrollTo(
            { y: this.scrollData.md[this.mdIndex].top },
            0
          );
        }, 10);
      }
    },
    enterList() {
      setTimeout(() => {
        this.$refs.vuescroll.scrollTo({ y: this.scrollData.list }, 0);
      }, 10);
    },
    handleRemoveCollect({ allIndex, removeIndex }) {
      this.removeCollect(allIndex, removeIndex);
    },
    handleCollectSkip({ allIndex, skipIndex }) {
      this.displayContent(allIndex);
      setTimeout(() => {
        this.$refs.vuescroll.scrollTo(
          { y: this.list.all[allIndex].collect[skipIndex].top },
          0
        );
      }, 10);
    },
    myScrollResize(...args) {
      if (this.showContentFlag == "md") {
        this.scrollData.md[this.mdIndex] =
          this.scrollData.md[this.mdIndex] || {};
        this.scrollData.md[this.mdIndex].size = args[2].height;
      }
    },
    myScrolling(scrolldata) {
      if (this.showContentFlag == "md") {
        this.scrollData.md[this.mdIndex] =
          this.scrollData.md[this.mdIndex] || {};
        this.scrollData.md[this.mdIndex].top =
          Math.round(
            (scrolldata.scrollTop / this.scrollData.md[this.mdIndex].size) * 100
          ) + "%";
      } else if (this.showContentFlag == "list") {
        // this.scrollData.list = scrolldata.scrollTop;
        setTimeout(() => {
          this.scrollData.list = scrolldata.scrollTop;
        }, 20);
      }
    },
    handleLongtapFooter(event) {
      event.preventDefault();
      if (this.showContentFlag == "md" &&( this.collectingFlag == false)) {
        this.collectingFlag = true;
        const allIndex = this.mdIndex;
        const top = this.scrollData.md[this.mdIndex].top || "0%";
        const time = new Date().getTime();
        this.list.all[allIndex].collect.push({
          top,
          time
        });
        setTimeout(()=>{
          this.collectingFlag = false;
        },8000);
        // this.openShade("收藏成功~");
        this.toLocalSave(allIndex);
      }
    },
    handleTapFooter() {
      if (this.showContentFlag != "list") {
        if (this.oldShowContentFlag == "collect" && (this.showContentFlag != "collect")) {
          this.oldShowContentFlag = this.showContentFlag;
          this.showContentFlag = "collect"; 
        } else {
          this.oldShowContentFlag = this.showContentFlag;
          this.showContentFlag = "list";
        }
        this.enterList();
      }
    },
    handleNavSwitch() {
      this.navFlag = !this.navFlag;
      if (this.navFlag) {
        this.openShade();
      }
    },
    handleTapIcon(event) {
      const fileName = event.target.title;
      const allIndex = this.byFileNameGetListAllIndex(fileName);
      if (this.list.all[allIndex].cacheFlag) {
        // 缓存
        this.displayContent(allIndex);
      } else {
        // 没缓存
        this.getFileContent(allIndex);
      }
    },
    handleLongtapIcon(event) {
      event.preventDefault();
      const fileName = event.target.title;
      const allIndex = this.byFileNameGetListAllIndex(fileName);
      if (this.list.all[allIndex].cacheFlag) {
        // 缓存
        this.removeClearCache(allIndex);
      } else {
        // 没缓存
      }
    },
    handleTapTitle(event, callee) {
      //点击标题
      const fileName = event.target.title;
      const allIndex = this.byFileNameGetListAllIndex(fileName);
      if (this.list.all[allIndex].cacheFlag) {
        // 缓存
        this.displayContent(allIndex);
      } else {
        // 没缓存
        window.open(this.baseUrl + fileName);
      }
    },
    handleLongtapTitle(event) {
      event.preventDefault();
      const fileName = event.target.title;
      this.openShade(fileName);
    },
    clearAndRefresh(){
      localStorage.clear();
      location.reload();
    },
    handleChangeNav(typeName) {
      this.closeShade();
      if (typeName == "function-init") {
        if (confirm("确定要清空所有缓存数据,并刷新吗？")) {
          this.clearAndRefresh();
        }
      } else if (/^#\*\*#(\d+)$/.test(typeName)) {
        this.collectIndex = /^#\*\*#(\d+)$/.exec(typeName)[1] - 0; //获得用户点击收藏夹目标文件名文件所在的allInddex；
        this.oldShowContentFlag = this.showContentFlag;
        this.showContentFlag = "collect";
      } else {
        this.handleTapFooter();
        this.showList = this.list["fileName_" + typeName];
        this.navFlag = false;
      }
    },
    openShade(content = "") {
      this.shadeFlag = true;
      this.shadeContent = content;
    },
    closeShade() {
      this.shadeFlag = false;
      this.shadeContent = "";
      this.navFlag = false;
    },
    displayContent(allIndex) {
      this.oldShowContentFlag = this.showContentFlag;
      if (
        this.list.all[allIndex].suffix == "md" ||
        this.list.all[allIndex].suffix == "txt"
      ) {
        this.showContentFlag = "md";
        this.mdIndex = allIndex;
        this.enterMd();
      } else {
        this.showContentFlag = "img";
        this.imgIndex = allIndex;
      }
    },
    getJSON() {
      //得到json
      // return nodelist;
      this.myJSON = Object.assign({}, node_list);
    },
    buildData() {
      //构造数据结构
      this.baseUrl = this.myJSON["baseUrl"];
      for (let n in this.myJSON) {
        if (n !== "baseUrl") {
          // 所有类型
          this.list.typeName.push(n);
          this.myJSON[n].forEach(item => {
            const localItem = this.getLocalObject(item);
            // 分类
            this.list["fileName_" + n] = this.list["fileName_" + n] || [];
            this.list["fileName_" + n].push(item);
            this.list["fileName_" + "all"] =
              this.list["fileName_" + "all"] || [];
            this.list["fileName_" + "all"].push(item);
            // 合并
            this.list.all = this.list.all.concat({
              fileName: item,
              suffix: this.$utils.getSuffix(item),
              cacheFlag: localItem.content ? true : false,
              cacheContent: localItem.content ? localItem.content : "",
              collect: localItem.collect ? localItem.collect : [],
              type: n
            });
          });
        }
      }
    },
    byFileNameGetListAllIndex(fileName) {
      let result = 0; //结果
      this.list.all.every((item, index) => {
        if (item.fileName == fileName) {
          result = index;
          return false;
        }
        return true;
      });
      return result;
    },
    removeCollect(listAllIndex, collectIndex) {
      //通过all的index，和收藏的引索，从localStorage删除
      const fileName = this.list.all[listAllIndex].fileName;
      const temp = this.getLocalObject(fileName);
      //本地删除
      if (!temp) return;
      if (!temp.collect.length) return;
      temp.collect.splice(collectIndex, 1);
      localStorage[fileName] = JSON.stringify(temp);
      //页面删除
      this.list.all[listAllIndex].collect.splice(collectIndex, 1);
    },
    removeClearCache(listAllIndex) {
      //删除整个文件的缓存
      localStorage.removeItem(this.list.all[listAllIndex].fileName);
      this.list.all[listAllIndex].collect = [];
      this.list.all[listAllIndex].cacheContent = "";
      this.list.all[listAllIndex].cacheFlag = false;
    },
    getFileContent(listAllIndex) {
      //请求内容并执行的缓存
      if (!this.checkCanCache(listAllIndex)) return alert("不允许缓存该类型！");
      this.loadingFlag = true;
      const url =
        this.baseUrl +
        this.list.all[listAllIndex].type +
        "/" +
        this.list.all[listAllIndex].fileName;
      new Promise((ok, no) => {
        if (
          this.list.all[listAllIndex].suffix == "md" ||
          this.list.all[listAllIndex].suffix == "txt"
        ) {
          this.$utils
            .ajax("GET", url)
            .then(res => {
              ok(res);
            })
            .catch(res => {
              no(res);
            });
        } else {
          this.$utils
            .imgToBese64(url)
            .then(res => {
              ok(res);
            })
            .catch(res => {
              no(res);
            });
        }
      })
        .then(res => {
          this.list.all[listAllIndex].cacheContent = res;
          this.list.all[listAllIndex].cacheFlag = true;
          this.toLocalSave(listAllIndex);
          this.loadingFlag = false;
        })
        .catch(res => {
          this.loadingFlag = false;
        });
    },
    toLocalSave(listAllIndex) {
      //通过all的index，保存到localStorage
      const temp = this.list.all[listAllIndex];
      localStorage[temp.fileName] = JSON.stringify({
        content: temp.cacheContent,
        collect: temp.collect
      });
    },
    checkCanCache(listAllIndex) {
      //检查是否允许缓存
      return this.allowCacheType.includes(this.list.all[listAllIndex].suffix);
    },
    getLocalObject(msg) {
      const fileName =
        typeof msg == "number" ? this.list.all[msg].fileName : msg;
      const temp = localStorage[fileName] || "{}";
      return JSON.parse(temp);
    }
  },
  created() {
    this.getJSON();
    this.buildData();
    this.showList = this.list.fileName_all;
  },
  mounted() {
    this.showContentFlag = "list";
    firstLoading.remove();
  },
  components: {
    IMG,
    MD,
    NodeNav,
    collectList,
    collectTip
  }
};
</script>

<style scoped lang="less">
@import url("../assets/list.less");
@green: green;
@keyframes loading {
  0% {
    background: #cc95c0;
  }
  33% {
    background: #dbd4b4;
  }
  67% {
    background: #7aa1d2;
  }
  100% {
    background: #cc95c0;
  }
}
@keyframes collecting {
  0% {
    background: #000;
  }
  50% {
    background: #04fc63;
  }
  100% {
    background: #000;
  }
}
.home {
  .header-footer {
    width: 100%;
    height: 50px;
    background: #000;
    color: #fff;
    position: relative;
    user-select: none;
    text-align: center;
    flex-direction: column;
    .switch {
      position: absolute;
      top: 10px;
      left: 0.2rem;
      width: 30px;
      height: 30px;
      line-height: 30px;
      cursor: pointer;
      border-radius: 40%;
      box-sizing: border-box;
      &:hover {
        border: 1px solid #666;
      }
      i {
        transition: all 0.4s;
      }
    }
    .deg180 {
      transform: rotate(180deg);
    }
    .subheading {
      font-size: 8px;
    }
  }
  .collecting{
    // background-color: green;
    animation: collecting 8s;
  }
  .main {
    width: 100%;
    height: calc(100% - 100px);
    position: relative;
    .node-nav {
      position: absolute;
      top: 0;
      left: -80%;
      transition: all 0.4s ease-in-out;
      z-index: 11;
    }
    .node-nav-active {
      left: 0;
    }
    .md {
      overflow-y: auto;
      overflow-x: hidden;
      background-color: #fafafa;
    }
    .img {
      overflow: hidden;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: #fafafa;
      flex-direction: column;
      // line-height: 30rem;
    }
  }
}
.shade {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.842);
  color: #fff;
  font-size: 30px;
  box-sizing: border-box;
  padding: 20px;
  text-align: center;
  z-index: 10;
}
</style>