<template>
  <div class="collect-tip">
    <div class="collect-warp">
      <div class="item" v-for="(n,index) in list.all[allIndex].collect" :style="{top:n.top}" :key="index"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "collectTip",
  props:{
    list:{
      type:Object,
      default:{}
    },
    allIndex:{
      type:Number,
      default:0
    }
  },
};
</script>

<style scoped lang="less">
.collect-tip {
  position:fixed;
  top:50px;
  right:0;
  width:10px;
  height:calc(100% - 100px);
  // background: #666;
  .collect-warp{
    position:relative;
    width:100%;
    height:100%;
  }
  .item{
    width:5px;
    height: 5px;
    border-radius: 50%;
    transform: translate(-50%,-50%);
    position: absolute;
    // top:50%;
    left:50%;
    background-color: rgb(253, 174, 174);
  }
}
</style>
